* {
  --gunmetal: #282c34;
  --isabelline: #e3e9ed;
  --sizzling-red: hsl(0, 85%, 62%);
  --green-blue-crayola: #058ed9;
  --green-pantone: #4daa57;
  --background-color: var(--isabelline);
  --primary: var(--green-pantone);
  --text-color: var(--gunmetal);
  --info: var(--green-blue-crayola);
}
button {
  cursor: pointer;
}
img.icon {
  font-size: 1.5em;
}
img.icon.large {
  font-size: 2em;
}
.App {
  background-color: var(--background-color);
  color: var(--text-color);
  height: 100vh;
  width: 100vw;
}
.App-Header {
  height: min-content;
  padding: 1em;
  display: flex;
  justify-content: space-between;
}
.Header-Menu {
  position: fixed;
  top: 3.5em;
  right: 2em;
  padding: 0.5em;
  background-color: var(--background-color);
  box-shadow: 0 0 3px 1px rgb(0 0 0 / 10%);
  border-radius: 0.3em;
  font-size: 0.85em;
  color: rgba(0,0,0,0.74);
}
.App-Logo {
  display: flex;
  align-items: center;
  font-size: 0.75em;
}
.App-Logo-img {
  height: 1.5rem;
  margin: 0 0.3em;
}
.App-Nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.Nav-Button {
  background-color: transparent;
  border: 0;
  display: grid;
  place-items: center;
  padding: 0.5em;
  clip-path: circle();
  transition: background-color 200ms;
}
.Nav-Button:hover,
.Nav-Button:active {
  background-color: rgba(0, 0, 0, 0.05);
}
.Recorder-Main {
  display: grid;
  grid-template-rows: auto 50px;
  height: calc(100vh - 80px);
}
.Recorder-Footer {
  width: 100%;
  height: 2.7em;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Record-Button {
  background-color: var(--sizzling-red);
  border: 0;
  color: white;
  padding: 0.7em;
  /* height: 1.5em;
  width: 1.5em; */
  clip-path: circle();
  display: grid;
  place-items: center;
  transition: filter 200ms;
  margin: 0 .5em;
  animation: grow-in 200ms;
}
.Record-Button.is-active {
  background-color: rgba(0, 0, 0, 0.08);
}
.Record-Button:hover,
.Record-Button:active {
  filter: brightness(0.85);
}
.Recordings-List-Main {
  height: 100vh;
}
.Recording-List-Header {
  padding: 1em;
}
.Back-Button {
  background-color: transparent;
  border: 0;
}
@keyframes grow-in {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}